import React, { useState, useEffect } from 'react';

function Introduce() {
    return (
        <div className="w19 introduce mark1 bg-lightSands" id="introduce">
            <div className="w15 introduce-box mark2">
                <p className="side-name">01 INTRODUCE | YUNJUNE ENGINEERING</p>
                <div className="introduce-contents">
                    <div className="introduce-text font-sans">
                        <p>
                            <strong>저희 홈페이지를 방문해주셔서 감사드립니다.</strong>
                            <br /><br />
                      윤준엔지니어링(주)이 '전력기술 엔지니어링'과 '신재생 에너지사업'의 파트너로 새롭게 출발합니다.
                      그동안 쌓아온 경험과 경륜을 합하여 고객만족을 위하여 최선을 다하겠습니다.
                      <br /><br />
                      대용량 태양광발전설비, 에너지저장장치(ESS), 풍력발전소 설계는 물론 다수의 초고압변전소와 송전선로 프로젝트를 수행한 경험을
                      바탕으로 최적의 발전설비와 송변전설비를 구축하는 파트너로서의 역할을 충실히 수행할 것 입니다.
                      <br /><br />

                      저희 임직원은 항상 고객만족을 위해 최선을 다하는 마음자세로 고객을 대하겠습니다. 풍부한 기술자료와 프로젝트 수행경험에서 체득된 노하우를
                      고객의 입장에서 생각하고 실천하도록 하겠습니다.
                      <br /><br />
                      전 세계 신재생에너지 글로벌 시장을 향하여 매진하도록 하겠습니다. 새롭게 시작하는 저희 윤준엔지니어링(주)을 응원해주시고 격려해주시길 부탁드립니다.
                      <br /><br />
                      감사합니다.
                      <br /><br />
                      이현구 배상
                        </p>
                        <div className="introduce-name">
                            <p className="introduce-name-main">대표이사 이현구</p>
                            <p className="introduce-name-sub">발송배전기술사 / 공학박사</p>
                        </div>
                    </div>
                    <div className="introduce-img">
                        <img src={require("../img/man.jpg").default} width="100%"></img>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Introduce;