import React, { useState, useEffect } from 'react';

function Business() {
    let [business, bs변경] = useState(['태양광 발전 설비','에너지 저장장치(ESS)','풍력발전','초고압 변전소','송전선로','소방 설비','건축전기','']);

    return (
        <div className="w19 business" id="business">
            <div className="w15 business-box mark2">
                <p className="side-name">02 BUSINESS | YUNJUNE ENGINEERING</p>
                <div className="main-titles business-title font-sans">
                    <p className="eng-title">BUSINESS</p>
                    <p className="kor-title">사업영역</p>
                </div>
                <div className="business-contents">
                    {
                        business.map((a,i)=>{
                            return <BusinessContent i={i} a={a} />
                        })
                    }
                </div>
            </div>
        </div>
    );
}

function BusinessContent(props){
    return (
        <div className={`business${props.i} business-content`}>
            <div className="business-img-box">
                <div className="business-img-border"></div>
                <img src={require(`../img/business${props.i}.jpg`).default}></img>
            </div>
            <p className="business-content-name">{props.a}</p>
            {/* <p className={`business-content-hidden business-hidden${props.i}`}>{props.a}</p> */}
        </div>
    )
}

export default Business;