import React, { useState, useEffect ,useRef } from 'react';
import './css/reset.css';
import './css/main.css';

import Header from './Components/Header';
import Footer from './Components/Footer';
import MainCarousel from './Components/MainCarousel';
import Introduce from './Components/Introduce';
import Business from './Components/Business';
import Map from './Components/Map';
import Licenses from './Components/Licenses';

function App() {
  let [pageY, setPageY] = useState(false);

  const contain = useRef(document);
  const handleScroll = () => {
    const { pageYOffset } = window;
    console.log(pageYOffset);
    if(pageYOffset > 100) {
      setPageY(true);
    } else setPageY(false);
  };
  useEffect(()=>{
    contain.current.addEventListener('scroll', handleScroll);
    return () => contain.current.removeEventListener('scroll', handleScroll);
  },[pageY]);

  return (
    <div className="App" >
      <div className="wrap">
        <Header pageY={pageY} />
        <MainCarousel />
        <div className="w19 intro">
          <div className="w15 intro-box font-sans mark">
            <p className="intro-text1 mr-au">
              세계 최대 수상태양광 발전설비 설계의 토탈 솔루션을 수행합니다
            </p>
            <p className="intro-text2 mr-au c-dg">
              "태양광 발전 설비 기본계획 및 실시 설계, 초고압 변전소 설계 및 송전선로 설계의 토탈 솔루션을 최고의 품질로 제공합니다"
            </p>
          </div>
        </div>
        <Introduce />
        <Business />
        <Licenses />
        <Map/>
        <Footer />
      </div>
    </div>
  );
}

export default App;
