import React, { useState, useEffect } from 'react';

function MainCarousel() {
    let [캐러셀리스트, 캐러셀리스트변경] = useState(['main1','main2','main3','main4']);
    let [캐러셀체크, 캐체변경] = useState(0);

    useEffect(()=>{
        let targetClass = document.querySelector('.mCarousel-list');
        
        const carouselTime = setTimeout(()=>{
            for(let i=0; i<4; i++){
                if(i == 캐러셀체크){
                    if(캐러셀체크 <3){
                        targetClass.classList.replace(`mc-${i}`,`mc-${i+1}`);
                        캐체변경(캐러셀체크+1);
                    }else{
                        targetClass.classList.replace(`mc-${i}`,`mc-0`);
                        캐체변경(0);
                    }
                }
            }
        }, 15000);

        return ()=> clearTimeout(carouselTime);
        
    },[캐러셀체크])


    const clickButton =(e) => {
        let id = e.target.id;
        let targetClass = document.querySelector('.mCarousel-list');
        console.log("캐러셀체크 = "+캐러셀체크);
        if(id=='left-button'){
            for(let i=0; i<4; i++){
                if(캐러셀체크 == i){
                    if(i != 0 ){
                        if(캐러셀체크 != 0){
                            캐체변경(i - 1);
                            targetClass.classList.replace(`mc-${캐러셀체크}`,`mc-${캐러셀체크-1}`);
                        } else {
                            캐체변경(0);
                        };
                    };
                };
            };
        }else{
            for(let i=0; i<4; i++){
                if(캐러셀체크 == i){
                    if(캐러셀체크 != 3){
                        캐체변경(i + 1);
                        targetClass.classList.replace(`mc-${캐러셀체크}`,`mc-${캐러셀체크+1}`);
                    } else {
                        캐체변경(3);
                    };
                };
            };
        };
    };

    return (
        <div className="w19 mCarousel">
            <div className="mCarousel-box">
                <div className="mCarousel-button left-button" id="left-button" onClick={(e)=>{clickButton(e)}}>〈</div>
                <div className="mCarousel-list mc-0">
                    {
                        캐러셀리스트.map((a, i) => {
                            return <CarouselContent 캐러셀리스트={캐러셀리스트} i={i} key={i} />
                        })
                    }
                </div>
                <div className="mCarousel-button right-button" id="right-button" onClick={(e)=>{clickButton(e)}}>〉</div>
            </div>
        </div>
    );
}

function CarouselContent(props) {
    return (
        <div className="mCarousel-content">
            <div className={`mCarousel-img mCarousel-img${props.i}`}>
                <img src={require(`../img/main_pic${props.i}.jpg`).default}></img>
                <div className="mCarousel-text">
                    <p class="font-20 fw-600 mct-side font-sans">전력기술 엔지니어링과 신재생 에너지사업의 파트너,</p>
                    <p class="font-35 mt-20 fw-600 mct-main font-sans">윤준 엔지니어링</p>
                    <p class="font-35 mt-20 fw-600 mct-side2 font-sans">글로벌 시장으로 나아갑니다.</p>
                </div>
            </div>
        </div>
    )
}


export default MainCarousel;