import React, { useState, useEffect, useRef } from 'react';

function Licenses() {
    let [라이센스, 라이센스변경] = useState();
    useEffect(()=>{
        class 라이센스생성 {
            constructor(i){
                this.num = i;
                if(i==0) this.name="감리업 등록증"
                else if(i==1) this.name="사업자 등록증"
                else if(i==2) this.name="설계업 등록증"
                else if(i==3) this.name="소방시설업 등록증"
                else if(i==4) this.name="지중케이블 특허증"
                else this.name="배전선로 특허증"
            }
        }

        var arr1 = [];
        function 객체반복생성(){
            var obj1 = {};
            for(let i=0; i<6; i++){
                obj1 = new 라이센스생성(i);
                arr1.push(obj1);
            }
            라이센스변경(arr1);
            return arr1;
        }
        객체반복생성();
    },[])

    return (
        <div className="w19 business mark1" id="licenses">
            <div className="w15 business-box mark2">
                <p className="side-name">03 LICENSES | YUNJUNE ENGINEERING</p>
                <div className="main-titles licenses-title font-sans">
                    <p className="eng-title">LICENSES</p>
                    <p className="kor-title">보유 면허</p>
                </div>
                <div className="licenses-contents">
                    {
                        라이센스 && 라이센스.map((a,i)=>{
                            return <LicenseContent a={a} i={i} />
                        })
                    }
                </div>
            </div>
        </div>
    );
}

function LicenseContent(props){
    const licenseInfo = useRef();

    const click_license = () => {
        let license_img = licenseInfo.current.firstChild.src;
        window.open(license_img);
    }

    return (
        <div className="licenses-content" onClick={()=>{click_license()}} ref={licenseInfo}>
            <img src={require(`../img/등록증${props.i}.jpg`).default} width="100%"></img>
            <div className="licenses-content-text">
                <p>{props.a.name}</p>
            </div>
        </div>
    )
}

export default Licenses;