/*global kakao*/ 
import React, { useEffect } from 'react'

const Map=()=>{

  useEffect(()=>{
    var container = document.getElementById('map');
    var options = {
      center: new kakao.maps.LatLng(37.47653964488099, 126.88133457306336),
      level: 3
    };
    var map = new kakao.maps.Map(container, options);
    var markerPosition  = new kakao.maps.LatLng(37.47653964488099, 126.88133457306336); 
    var marker = new kakao.maps.Marker({
      position: markerPosition
    });
    marker.setMap(map);
    }, [])


    return (
      <div className="w19 business mark1">
        <div className="w15 business-box mark2">
          <p className="side-name">04 ADDRESS | YUNJUNE ENGINEERING</p>
          <div className="main-titles business-title font-sans">
            <p className="eng-title">ADDRESS / CONTACT</p>
            <p className="kor-title">오시는길 / 연락처</p>
          </div>
          <div className="map-contents">
            <div id="map" className="map-contents-left"></div>
            <div className="map-contents-right bg-fern">
              <div className="mcr-box">
                <p className="mcr-title font-sans">Seoul, KOREA</p>
                <p className="mcr-addr">1-608, Lotte IT castle, 98, Gasandigital 2-ro, Geumcheon-gu, Seoul, 08506, Korea
                  <br/>우08506 서울특별시 금천구 가산디지털2로 98, 1동 608호
                </p>
                <p className="mcr-phone">대표번호 : +82 02 851 8030</p>
                <p className="mcr-email">문의 : hglee333@hanmail.net</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
}

export default Map;